import service from '../../service.js';
import mInput from '../../components/m-input.vue';

export default {
	components: {
		mInput
	},
	data() {
		return {
			userData: {},
			nickName: '',
			userName: '',
			password: '',
			password1: '',
			phoneNumber: '',
			issuccess:''
		};
	},
	methods: {
		
		register() {
			let that = this;
			console.log(that);
			uni.request({
				url: 'http://121.41.93.162:8080/travel-0.0.1-SNAPSHOT/register/user',
				method: 'POST',
				data: {
					nickName: that.nickName,
					userName: that.userName,
					password: that.password,
					phoneNumber: that.phoneNumber
				},
				success: res => {
					if(res.data.msg == "OK") {
					    uni.showToast({
					        title: '注册成功',
					        icon: 'success',
					        duration: 2000
					    })
						setTimeout(()=>{uni.hideToast();},500)
						uni.navigateBack({
							delta: 1
						});
					} else {
					    uni.showToast({
					        title: "该账号已被注册",
					        duration: 2000
					    })
						setTimeout(()=>{uni.hideToast();},1000)
					}
					console.log(res.data.msg);
				},
				fail: () => {},
				complete: () => {}
			});

			if (this.nickName.length > 12) {
				uni.showToast({
					icon: 'none',
					title: '昵称最长为 12 个字符'
				});
				return;
			}
			if (this.userName.length < 2) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 2 个字符'
				});
				return;
			}

			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			if (this.password !== this.password1) {
				uni.showToast({
					icon: 'none',
					title: '俩次密码不一致'
				});
				setTimeout(()=>{uni.hideToast();},500)
				return;
			}

			if (this.phoneNumber !== !/^1(3|4|5|6|7|8|9)\d{9}$/ && this.phoneNumber.length != 11) {
				uni.showToast({
					icon: 'none',
					title: '手机号码不合法'
				});
				setTimeout(()=>{uni.hideToast();},500)
				return;
			}
		}
	},
	onUnload: function() {}
};
